}

.header {
  color: #fff;
  font-size: 14px;
  font-weight: 700;
}

.header.default {
  height: 60px;
  z-index: 10;
  background-color: #fff;
  position: fixed;
  width: 100%;
  color: #000;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 3px;
}

.header.default .header_row {
  height: 60px;
}

.header.default .header_tel:hover {
  background-color: transparent;
  color: #2f7dab;
}

.header .container {
  position: relative;
}

.header_row {
  position: absolute;
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 3;
}

.header_nav--a {
  margin: 0 15px;
  text-transform: uppercase;
  position: relative;
}

.header_nav--a::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 20%;
  opacity: 0;
  width: 100%;
  height: 100%;
  border-bottom: 1px solid #2f7dab;
  box-shadow: inset 0px -1px 0px 0px #2f7dab;
  transition: all 0.3s ease;
}

.header_nav--a:hover::after {
  opacity: 1;
  bottom: -1px;
}

.header_tel {
  height: 40px;
  padding: 0 30px;
  border-radius: 20px;
  line-height: 40px;
  text-transform: uppercase;
  transition: .3s all;
  margin-right: 50px;
}

.header_tel:hover {
  background-color: #2f7dab;
}

.btn_mnu {
  opacity: 0;
  width: 50px;
  height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: fixed;
  right: 25px;
  top: 25px;
  z-index: 11;
  transition: .3s all;
}

.btn_mnu.active .btn_row:first-child {
  transform: rotate(-135deg);
  top: 1px;
}

.btn_mnu.active .btn_row:nth-child(2) {
  transform: rotate(-225deg);
  top: -6px;
}

.btn_mnu.active .btn_row:last-child {
  opacity: 0;
}

.btn_row {
  height: 2px;
  width: 60%;
  margin: 2px 0;
  background-color: #2f7dab;
  position: relative;
  transition: all .5s;
}

.menu {
  opacity: 0;
  background-color: #000;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  transition: .3s all;
}

.menu.active {
  opacity: 1;
  z-index: 10;
}

.menu_nav {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.menu_nav--item {
  margin-top: 20px;
  color: #ffffff;
  font-size: 16px;
  font-weight: 500;
}

