  bottom: -5px;
  right: -5px;
  background-color: #2f7dab;
  width: 30px;
  height: 30px;
  text-align: center;
  font-size: 14px;
  line-height: 30px;
  color: #fff;
  border-radius: 100%;
}

.basket_sum {
  opacity: 0;
  transition: .3s all;
  position: absolute;
  top: 50%;
  left: -100%;
  transform: translate(-50%, -50%);
  background: #292929;
  color: #fff;
  font-size: 15px;
  box-sizing: border-box;
  padding: 9px 13px;
  border-radius: 3px;
  font-weight: 600;
}

.basket_sum::after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  border: solid transparent;
  border-width: 10px;
  top: 50%;
  right: -20px;
  transform: translateY(-50%);
  border-left-color: #292929;
}

.wh {
  position: fixed;
  bottom: 15px;
  right: 5px;
  display: flex;
  align-items: center;
  z-index: 9;
}

.wh_btn {
  cursor: pointer;
  width: 160px;
  height: 40px;
  color: #ffffff;
  font-size: 14px;
  font-family: 'Montserrat', 'Arial', sans-serif;
  line-height: 1.55;
  font-weight: 700;
  border-width: 1px;
  border-radius: 30px;
  background-color: #2f7dab;
  transition: .3s all;
}

.wh_btn:hover {
  transform: scale(1.2);
}

.wh_img {
  margin-left: 20px;
  width: 40px;
  height: 40px;
  transition: .3s all;
}

.wh_img:hover {
  transform: scale(1.2);
}

.wh_form {
  position: absolute;
  bottom: 70px;
  right: 65px;
  width: 329px;
  height: 240px;
  padding: 15px 10px;
  background-color: #fff;
  border-radius: 5px;
  z-index: 9;
  opacity: 0;
  transition: .3s all;
}

.wh_form.active {
  opacity: 1;
}

.wh_form--action {
  display: flex;
  flex-direction: column;
}

.wh_form--area {
  color: #454545;
  border: 1px solid #2f7dab;
  background-color: #fff;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 400;
  height: 85px;
  padding: 10px 20px;
}

.wh_form--input {
  height: 40px;
  margin-top: 25px;
  padding: 0 20px;
  color: #454545;
  border: 1px solid #2f7dab;
  background-color: #fff;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 400;
}

.wh_form--input::placeholder {
  color: #454545;
  font-size: 14px;
  font-weight: 400;
}

.wh_form--btn {
  align-self: flex-start;
  margin-top: 20px;
  cursor: pointer;
  width: 160px;
  height: 40px;
  color: #ffffff;
  font-size: 14px;
  font-family: 'Montserrat', 'Arial', sans-serif;
  line-height: 1.55;
  font-weight: 700;
  border-width: 1px;
  border-radius: 30px;
  background-color: #2f7dab;
}

